@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
body {
  margin: 0;
  padding-right: -5px;
  width: 100%;
  max-width: 100% !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/film-background.fc72d862.png);
  background-size: auto;
  overflow: clip !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Rubik', sans-serif;
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  margin-top: 4em;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-wrap: no-wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
}

#optionselector {
  display: flex;
  flex-direction: column;
  background-color: rgb(240,100,100);
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  margin: 30px 0;
}

#getmoviesbutton {
  height: 40px;
  width: 180px;
  color: white;
  font-size: 1em;
  border-radius: 5px;
  background-color: rgb(240,100,100);
  margin: 0 auto 1em auto;
}

#getmoviesbutton:hover {
  cursor: pointer;
}

.pointer {
  cursor: pointer;
}

#githublink {
  margin: 20px 20px 0px 20px;
  font-size: .8rem;
  padding-bottom: 5px;
  cursor: default;
  left: 50%;
  bottom: 10px;
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

#githublink a {
  cursor: pointer;
  color: rgb(187, 30, 30);
}

@media only screen and (max-width: 800) {

  body {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    justify-items: center;
  }

  .app {
    overflow: hidden;
  }
    #optionselector {
      overflow: hidden;
      width: 90%;
      padding: 10px;
    }
}
.dropdown-container {
    display: inline-flex;
}

h3 {
   cursor: default;
}

select {
    font-family: 'Rubik', serif;
    height: 30px;
    margin: 15px 4px;
    font-size: .8em;
}

@media only screen and (max-width: 800px) {
  .dropdown-container h3 {
      font-size: 1rem;
    }

    .dropdown-container {
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: 90%;
      flex-wrap: wrap;
      text-align: center;
    }
  }
#movie-container {
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap;
    min-width: 300px;
    max-width: 77rem;
    background-color: whitesmoke;
    padding: 0;
    border: 1px solid gray;
    border-radius: 10px;
    margin: 14px 20px;
    box-shadow: 2px -2px rgb(220,220,220);
    overflow: hidden;
}

h1 {
    font-size: 1.5em;
    text-align: center;
}

.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.flex-stretch {
    display: flex;
    flex-direction: column;
    justify-items: space-between;
}

.left {
    flex-grow: 1 1 auto;
    min-width: 300px;
    max-width: 30rem;
    padding: 0;
}

.middle {
    flex-grow: 1;
    width: 15rem;
    margin: 0;
    border-left: 1px solid gray;
    padding: 0;
    flex-shrink: 0;
}

.right {
    min-width: 250px;
    height: 350px;
    max-width: 25rem;
    margin: 0;
    padding: 0;
}

.header {
    color: white;
    margin-top: 0;
    min-height: 5rem;
    height: 5rem;
    background-color: rgb(240,100,100);
}

.body {
    padding: 5px;
    min-height: 1rem;
    margin: 0;
}

.mainrating {
    display: flex;
    font-size: 2.5em;
    font-style: italic;
}

.mainrating-container {
    margin: auto;
    padding-top: 18px;
}

.body p {
    padding: 15px 5px 0px 5px;
}

.plot {
    display: flex;
    flex-direction: column;
    flex: 1 2 auto;
    font-size: 1.2em;
    text-align: justify;
    padding: 2px 15px;
}

.poster {
    min-height: 6rem;
    max-width: 6rem;
    font-weight: bold;
}

.ratings {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-weight: bold;
}

.logos {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding-top: 8px;
    width: 2rem;
}

.logos img {
    margin-right: 8px;
}

.releaseruntime {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.footer {
    margin-top: 0;
    padding: 20px 5px;
    min-height: 4rem;
    flex-grow: 1;
    margin-bottom: 0;
}

@media only screen and (max-width: 900px) {
    .left {
        flex-grow: 1;
        max-width: 100%;
    }
}

@media only screen and (max-width: 500px) {
    .movie-container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 10px 0;
    }
    .ratingheader {
        display: none;
    }

    .header {
        margin: 0rem 0 1rem 0;
        padding: 10px;
    }
    .middle {
        border-left: none;
    }

    .plot {
        flex: 1 1 200px;
        align-items: flex-start !important;
        padding: 0px 5px;
        margin: -20px 8px 0px 8px;
    }

    .body {
        height: 80px;
        padding: 4px;
    }

    .releaseruntime {
        flex: 0 1 auto;
        width: 80%;
        font-size: 1.1rem;
        flex-wrap: wrap;
        margin: -20px 10px 0 10px;
    }

    .mainrating-container {
        margin: -30px auto;
        padding: -10px;
        height: 80px !important;
    }
    .mainrating {
        margin: auto;
        height: 60px;
    }

    .separator {
        display: none;
    }

    .poster {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 95%;
        max-width: 95%;
        height: 30rem;
        margin-bottom: .5rem;
    }
}
#MovieResults {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-items: center;
    flex-wrap: wrap;
    width: 100%;
}

#pagebutton-container {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.pagebutton {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  color: white;
  text-align: center;
  font-size: 1em;
  font-weight: bold;
  border-radius: 5px;
  background-color: rgb(240,100,100);
  margin: 1em .2em;
}
